import { useState } from "react";
import axios from "axios";

export default function CreateChat() {
  const [title, setTitle] = useState("");
  const [response, setResponse] = useState(null);
  const [error, setError] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setResponse(null);

    const token = localStorage.getItem("token");
    if (!token) {
      setError("Anda harus login terlebih dahulu.");
      return;
    }

    try {
      const res = await axios.post("http://localhost:3000/api/history/chat", { title }, {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
      });
      setResponse(res.data);
    } catch (err) {
      setError(err.response?.data?.message || "Terjadi kesalahan");
    }
  };

  return (
    <div>
      <h2>Buat Chat Baru</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          placeholder="Masukkan judul chat"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          required
        />
        <button type="submit">Buat Chat</button>
      </form>
      {error && <p style={{ color: "red" }}>{error}</p>}
      {response && <p>Chat berhasil dibuat dengan ID: {response._id}</p>}
    </div>
  );
}
